$black: #333;
$purple: #6d639a;
$pink: #d63f74;
$white: #fefefe;
$grey: rgba($black, .5);

*{
	font-family: Bungee Hairline !important;
	font-weight: bold;
	color: $black;
	border-color: $black; 
    line-height: 1.5;
}

\::-webkit-input-placeholder
	{color: rgba($pink, .5)}

\:-moz-placeholder
	{color: rgba($pink, .5)}

\::-moz-placeholder
	{color: rgba($pink, .5)}

\:-ms-input-placeholder
	{color: rgba($pink, .5)}

body
	{background: #fefefe}

.textCenter
	{text-align: center}

.removeBtn{
	position: absolute;
	right: 8px;
	top: 50%;
    transform: translateY(-50%)
}

#container{
	margin: auto;
	text-align: center;
	max-width: 700px;
	margin: 60px auto 0 auto;
	padding: 0px 20px;
	
	h2{
		margin-bottom: 30px;
		color: $black;
	}
	#form{
		margin-bottom: 50px
	}	
	#info{
		padding: 7px;
		text-align: left;
		color: $grey;
    }      
	input{
		background: transparent;
		border: solid 1px;
		padding: 7px 10px;
		border-right: none;
		width: 70%;
		color: $black;
		border-color: $pink;
		outline: none;
    }
	button{
		border: solid 1px $pink;
		padding: 7px 10px;
		background: rgba($pink, .8);
		cursor: pointer;
		width: 15%;
		min-width: 20px;
		transition: all 100ms linear 80ms;
		color: $white;
		line-height: 1;
		font-size: 18px;
		outline: none;

	}
	button, input{
		vertical-align: middle;
		height: 35px;
		box-sizing: border-box;
    }
    input[type='checkbox']{
		vertical-align: middle;
		height: 20px;
		box-sizing: border-box;
	}
	#acu{
		text-align: left;
		padding: 7px;
	}
	#footer{
		margin: 50px 0 30px 0;
		padding-top: 30px;
		text-align: left;
		border-top: dotted 2px $pink;
		p{
			padding: 7px;
			display: inline-block;
			color: $black;
			transition: all 100ms linear 80ms
			// &:hover
			// 	color: $pink
			// 	&:before, &:after
			// 		color: $purple
			// &:before
			// 	content: '<'
			// &:after
            //     content: '/>';
         }       
	}	
	.todos{
		transition: all 100ms linear 100ms;
		position: relative;
		text-align: left;
		margin: 30px auto;
		padding: 7px;
		padding-right: 28px;
		border: solid 1px transparent;
		color: $purple
	}	
		// &:hover
		// 	cursor: pointer
		// 	border: solid 1px  
		// 	span
		// 		visibility: visible
		// 		color: inherit
		
		// span
		// 	transition: all 100ms linear 100ms	
		// 	visibility: hidden
		// 	&:hover
		// 		cursor: pointer
}		
			